// ====================================================================
// Layouts SCSS for barrd.net
// ====================================================================

#diamond-list {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 2;
  @media (min-width: $breakpoint-width-large) and (min-height: $breakpoint-height-large) {
    height: 97vh;
  }
}

#big-diamond {
  width: $grid-width;
  @media (min-width: $breakpoint-width-large) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    transform: rotate(45deg);
  }
  @media (max-height: $breakpoint-height-small) and (min-width: $breakpoint-width-small) {
    margin-top: 130px;
  }
  @media (max-width: $breakpoint-width-small) {
    width: $cell-width;
  }
}

.diamond {
  background-image: linear-gradient(
    -45deg,
    $color-light-orange,
    $color-mid-orange
  );
  display: flex;
  align-items: center;
  justify-content: center;
  width: $cell-width;
  height: $cell-height;
  box-shadow: 6px 6px 6px 1px $color-dark-text;
  transition: all 0.4s ease-in-out;
  border: 0;
  @media (max-width: $breakpoint-width-small) {
    margin-top: 82px;
    transform: rotate(45deg);
    box-shadow: none;
  }

  & .diamond-content {
    display: flex;
    align-items: center;
    justify-content: center;
    transform: rotate(-45deg);
    width: $cell-width;
    height: $cell-height;

    span {
      font-size: 1.25rem;
      text-align: center;
      color: $color-dark-text;
      transition: 0.75s ease-in-out;
      opacity: 0;
    }

    &:hover span {
      opacity: 1;
    }
  }

  &:hover {
    box-shadow: -6px -6px 6px 1px $color-dark-text;
    @media (max-width: $breakpoint-width-small) {
      box-shadow: none;
    }
  }

  %diamond-top {
    background-repeat: no-repeat;
    background-position: center center;
    background-size: $bg-width $bg-width;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
    content: "";
    opacity: 0.9;
    transition: 0.4s ease-in-out;
    pointer-events: auto !important;
    @media (max-width: $breakpoint-width-small) {
      background-size: $bg-width-small $bg-width-small;
      opacity: 0.95;
    }
  }

