.diamond-top:hover:after,
.diamond-right:hover:after,
.diamond-left:hover:after,
.diamond-bottom:hover:after {
  opacity: 0.25;
  @media (max-width: $breakpoint-width-small) {
    opacity: 1;
  }
}

%external-box {
  position: absolute;
  pointer-events: none;
  transform: rotate(-90deg);
  color: $color-dark-text;
}

.site-link {
  @extend %external-box;
  top: 110px;
  left: -95px;
  font-size: 1.25rem;
}

footer {
  @extend %external-box;
  bottom: 86px;
  left: 327px;
  white-space: nowrap;
  @media (max-width: $breakpoint-width-small) {
    bottom: 86px;
    left: 122px;
  }
}
