// ====================================================================
// Reset SCSS for barrd.net
// ====================================================================

html,
body,
div,
span,
h1,
h2,
h3,
p,
a,
img,
center,
article,
footer,
header,
menu,
nav {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
// HTML5 display-role reset for older browsers
article,
footer,
header,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
